(function () {
  angular
    .module('app')
    .component('staticComponent', {
      templateUrl: 'app/components/categories/static/static.component.html',
      controller: staticComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function staticComponent(StaticFields, $stateParams, toastr, $state, $uibModal) {
    var vm = this;
    vm.$onInit = function () {
      vm.model = $stateParams.model[0].toUpperCase() + $stateParams.model.slice(1);
      vm.staticFields = StaticFields.static[vm.model];
      if (!vm.staticFields){
        toastr.error('Static table with name ' + vm.model + ' not found', 'Error');
        $state.go('categories');
      }
      StaticFields.getFields(vm.model)
        .then(function (response) {
          vm.fields = response.data;
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });
    };
    vm.openFieldModal = function (field, index) {
      if(!field.model) field.model = vm.model;
      field.required = !!field.required;
      var modalInstance = $uibModal.open({
        animation: true,
        component: 'createStaticFieldComponent',
        resolve: {
          field: function () {
            return angular.copy(field);
          }
        }
      });

      modalInstance.result.then(function (result) {
        if(field._id === result._id) {
          vm.fields[index] = result;
        } else {
          vm.fields.push(result);
        }
      });
    };
    vm.delete = function (field, index) {
      $uibModal.open({
        animation: true,
        component: 'confirmComponent',
        size: 'sm',
        resolve: {
          message: function () {
            return 'Are you sure to delete field ' + field.title + '?';
          }
        }
      }).result.then(function () {
        StaticFields.deleteField(field)
          .then(function () {
            vm.fields.splice(index, 1);
          })
          .catch(function () {
            toastr.error('Something went wrong', 'Error');
          })
      });
    }
  }
})();

(function () {
  angular
    .module('app')
    .component('createCategoryComponent', {
      templateUrl: 'app/components/categories/create/create.category.component.html',
      bindings: {
        resolve: '<',
        close: '&',
        dismiss: '&'
      },
      controller: createCategoryComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function createCategoryComponent(Category, toastr) {
    var vm = this;
    vm.$onInit = function () {
      vm.category = vm.resolve.category;
      Category.getCategoriesForSub()
        .then(function (response) {
          vm.relationCategories = response.data;
          if (vm.category._id && vm.category.type === 'Table with subcategory') {
            for(var i = 0; i < vm.relationCategories.length; i++){
              if(vm.relationCategories[i]._id === vm.category.relatedCategory){
                vm.category.relatedCategory = vm.relationCategories[i];
                break;
              }
            }
            for(i = 0; i < vm.category.relatedCategory.fields.length; i++){
              if(vm.category.relatedCategory.fields[i]._id === vm.category.subcategoryField){
                vm.category.subcategoryField = vm.category.relatedCategory.fields[i];
                break;
              }
            }
          }
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });

      vm.categoryType = ['Table', 'Attachments', 'Gallery', 'Table with subcategory'];
    };

    vm.clearRelatedCategory =function () {
      delete vm.category.relatedCategory;
      vm.clearRelatedField();
    };

    vm.clearRelatedField = function () {
      delete vm.category.subcategoryField;
    };

    vm.cancel = function () {
      vm.dismiss({$value: 'cancel'});
    };

    vm.save = function () {
      vm.loadInProgress = true;
      if(vm.category._id){
        Category.updateCategory(vm.category)
          .then(function () {
            vm.close({$value: vm.category});
          })
          .catch(function (error) {
            var message;
            if (error.data && error.data.message) {
              message = error.data.message;
            } else {
              message = 'Something went wrong';
            }
            toastr.error(message, 'Error');
          })
          .finally(function () {
            vm.loadInProgress = false;
          });
      } else {
        Category.createCategory(vm.category)
          .then(function (response) {
            vm.close({$value: response.data});
          })
          .catch(function (error) {
            var message;
            if (error.data && error.data.message) {
              message = error.data.message;
            } else {
              message = 'Something went wrong';
            }
            toastr.error(message, 'Error');
          })
          .finally(function () {
            vm.loadInProgress = false;
          });;
      }
    }
  }
})();

