(function () {
  angular
    .module('app')
    .component('loginComponent', {
      templateUrl: 'app/components/login/login.component.html',
      controller: LoginComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function LoginComponent(Auth, $state, toastr) {
    var vm = this;
    vm.$onInit = function () {
      Auth.logOut();
      vm.data = {};
    };
    vm.login = function () {
      Auth.login(vm.data)
        .then(function (currentUser) {
          if(currentUser.role === 'salesAdmin' || currentUser.role === 'salesUser'){
            $state.go('serials')
          }
          else if(currentUser.role === 'support'){
            $state.go('rma')
          }
          else $state.go('home')
        })
        .catch(function (error) {
          var message;
          if (error.data && error.data.message) {
            message = error.data.message;
          } else {
            message = 'Something went wrong';
          }
          toastr.error(message, 'Error');
        });
    };
  }

})();

(function () {
  angular
    .module('app')
    .component('importComponent', {
      templateUrl: 'app/components/import/import.component.html',
      bindings: {
        resolve: '<',
        close: '&',
        dismiss: '&'
      },
      controller: importComponent,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function importComponent() {
    var vm = this;
    vm.$onInit = function () {
      vm.loadInProgress = false;
      vm.headers = vm.resolve.headers;
      vm.data = [];
      vm.editedData = [];
      vm.pagination = {
        limit: 50,
        parts: 0,
        total: 0,
        part: 0
      };
    };
    vm.onParseStarted = function () {
      vm.loadInProgress = true;
    };

    vm.onDataChanged = function (data) {
      vm.data = data;
      if(vm.resolve.parser) {
        vm.resolve.parser(vm.data);
      }
      vm.pagination.total = vm.data.length;
      vm.pagination.part = 0;
      vm.pagination.parts = Math.ceil(vm.pagination.total / vm.pagination.limit);
      vm.nextPart();
      vm.loadInProgress = false;
    };

    vm.nextPart =function () {
      vm.pagination.part++;
      vm.editedData = vm.data.splice(0, vm.pagination.limit);
    };

    vm.save = function () {
      vm.loadInProgress = true;
      vm.resolve.save(vm.editedData)
        .then(function () {
          if(!vm.data.length) {
            vm.close();
          } else {
            vm.nextPart();
          }
        })
        .finally(function () {
          vm.loadInProgress = false;
        });
    };

    vm.cancel = function () {
      vm.dismiss({$value: 'cancel'});
    };
  }
})();

(function () {
  angular
    .module('app')
    .component('homeComponent', {
      templateUrl: 'app/components/home/home.component.html',
      controller: HomeController,
      controllerAs: 'vm'
    });

  /** @ngInject */
  function HomeController(localStorageService, Device, toastr, $uibModal) {
    var vm = this;

    vm.$onInit = function () {
      // Device.generate();
      vm.devices = [];
      vm.sortFilters = localStorageService.get('deviceSortFilters') || {
        sort: 'model',
        order: true
      };
      vm.limits = [10, 20, 50, 100];
      vm.pagination = {
        page: 1,
        limit: localStorageService.get('paginationLimit') || 10
      };
      vm.filters = localStorageService.get('deviceFilters') || {};
      vm.loadDevices();
    };

    vm.onLimitChange = function () {
      localStorageService.set('paginationLimit', vm.pagination.limit);
      vm.loadDevices();
    };

    vm.onFiltersChanged  = function (filters) {
      localStorageService.set('deviceFilters', filters);
      vm.filters = filters;
      vm.loadDevices();
    };

    vm.onSortFiltersChanged = function (key) {
      if(vm.sortFilters.sort === key){
        vm.sortFilters.order = !vm.sortFilters.order;
      } else {
        vm.sortFilters.sort = key;
        vm.sortFilters.order = true;
      }
      localStorageService.set('deviceSortFilters', vm.sortFilters);
      vm.loadDevices();
    };

    vm.loadDevices = function () {
      var query = '?';
      for(var key in vm.filters) {
        if(vm.filters[key]){
          query = query + key + '=' + vm.filters[key] + '&'
        }
      }
      query = query + 'sort=' + (vm.sortFilters.order ? '' : '-') + vm.sortFilters.sort + '&';
      query = query + 'page=' + vm.pagination.page + '&';
      query = query + 'limit=' + vm.pagination.limit;
      Device.getAll(query)
        .then(function (response) {
          vm.devices = response.data.docs;
          vm.pagination.total = response.data.total;
          vm.pagination.page = response.data.page;
        })
        .catch(function () {
          toastr.error('Something went wrong', 'Error');
        });
    };

    vm.openDeviceModal = function (device, index) {
      var modalInstance = $uibModal.open({
        animation: true,
        component: 'createDeviceComponent',
        resolve: {
          device: function () {
            return angular.copy(device);
          }
        }
      });

      modalInstance.result.then(function (result) {
        // if(device._id === result._id) {
        //   vm.devices[index] = result;
        // } else if (applyByFilters(result)){
        //   vm.devices.push(result);
        // }
        vm.loadDevices();
      });
    };
    vm.delete = function (device, index) {
      $uibModal.open({
        animation: true,
        component: 'confirmComponent',
        size: 'sm',
        resolve: {
          message: function () {
            return 'Are you sure to delete device ' + device.model + '?';
          }
        }
      }).result.then(function () {
        Device.deleteDevice(device)
          .then(function () {
            // vm.devices.splice(index, 1);
            vm.loadDevices();
          })
          .catch(function () {
            toastr.error('Something went wrong', 'Error');
          })
      });
    };
    // function applyByFilters(device) {
    //   var apply = true;
    //   for (var key in vm.filters) {
    //     if(vm.filters[key].length && device[key].indexOf(vm.filters[key]) === -1) {
    //       apply = false;
    //       break;
    //     }
    //   }
    //   return apply;
    // }
  }
})();

