(function () {
  angular
    .module('app')
    .service('Auth', Auth);

  /** @ngInject */
  function Auth($http, localStorageService, $q) {
    var self = this;
    self.isLoggedin = !!localStorageService.get('token');
    var currentUser = {};

    if (self.isLoggedin) {
      currentUser = me();
    }

    self.login = function (data) {
      return $http.post('/api/auth/local', data)
        .then(function (response) {
          localStorageService.set('token', response.data.token);
          self.isLoggedin = true;
          currentUser = me();
          return currentUser;
        });
    };

    self.getCurrentUser = function () {
      return $q.when(currentUser);
    };

    self.logOut = function () {
      localStorageService.remove('token');
      localStorageService.remove('deviceFilters');
      localStorageService.remove('deviceSortFilters');
      self.isLoggedin = false;
      currentUser = {};
    };

    function me() {
      return $http.get('/api/users/me')
        .then(function (response) {
          return response.data;
        });
    }
  }

})();

(function () {
  angular
    .module('app')
    .factory('AuthInterceptor', AuthInterceptor);

  /** @ngInject */
  function AuthInterceptor (localStorageService, $q, $injector) {
    return {
      request: function (config) {
        config.headers = config.headers || {};
        config.headers['authorization'] = 'Bearer ' + localStorageService.get('token');
        return config;
      },
      responseError: function (response) {
        if(response.status === 401){
          var state = $injector.get('$state');
          if(state.$current.name !== 'login') state.go('login');
        }
        return $q.reject(response);
      }
    };
  }
})();

angular
  .module('app')
  .filter('nthChild', function () {
    return function (items, nth) {
      if(!items || !angular.isArray(items)) return items;
      return items.filter(function (item, index) {
        return index % 2 === nth;
      });
    };
  });

(function () {
  angular
    .module('app')
    .directive('hasRole', hasRole);

  /** @ngInject */
  function hasRole(Auth) {
    return {
      restrict: 'A',
      scope: {
        hasRole: '<'
      },
      link: function (scope, element, attr) {
        Auth.getCurrentUser()
          .then(function (user) {
            if(scope.hasRole.indexOf(user.role) === -1){
              element.hide();
            }
          })
      }
    }
  }
})();

(function () {
  angular
    .module('app')
    .directive('fileread', function () {
      return {
        scope: {
          headers: '<',
          onDataChanged: '&',
          parseStarted: '&'
        },
        link: function ($scope, $elm, $attrs) {
          $elm.on('change', function (changeEvent) {

            $scope.$apply(function () {
              $scope.parseStarted();
            });
            var reader = new FileReader();

            reader.onload = function (evt) {
              var data = evt.target.result;
              var workbook = XLSX.read(data, {type: 'binary', cellDates: true});
              data = XLSX.utils.sheet_to_json(
                workbook.Sheets[workbook.SheetNames[0]],
                {
                  header: $scope.headers.map(function (item) {
                    return item.key
                  })
                }
              );

              $scope.$apply(function () {
                $scope.onDataChanged({data: data});
                $elm.val(null);
              });
            };
            reader.readAsBinaryString(changeEvent.target.files[0]);
          });
        }
      }
    })
})();

